.header{
  position: relative;
  z-index: 5;
  &__logo{
    display: block;
    width: 9.75em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top{
  padding: .875em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link,
  &__item{
    @include lastM(0 1.875em 0 0);
  }
  &__sublink,
  &__link,
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      color: $yellow;
      path{
        fill: $yellow;
      }
    }
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
  &__show{
    svg{
      width: .375em;
      height: auto;
      flex-shrink: 0;
      margin-left: .31em;
    }
  }
  &__item{
    position: relative;
    &._show{
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__hidden{
    &-inner{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    @media screen and (min-width: 993px){
      position: absolute;
      top: 99%;
      left: -1em;
      padding-top: .5em;
      z-index: 3;
      min-width: 12em;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      &-inner{
        box-shadow: 0 0 0 0 rgba(0,0,0,.1),0 8px 18px 0 rgba(0,0,0,.1),0 33px 33px 0 rgba(0,0,0,.09),0 75px 45px 0 rgba(0,0,0,.05),0 134px 54px 0 rgba(0,0,0,.01),0 209px 59px 0 transparent;
        padding: 1em;
        background-color: #fff;
        border-radius: .4375em;
        max-height: 20em;
        overflow-y: auto;
      }
    }
  }
  &__sublink{
    white-space: nowrap;
    @include lastM(0 0 .5em);
  }
}

.header-address{
  display: flex;
  align-items: center;
  margin-right: 1.875em;
  img{
    width: 1em;
    flex-shrink: 0;
    margin-right: .19em;
  }
  &__text{
    span{
      font-size: .75em;
      line-height: 125%;
    }
    sup{
      font-weight: 600;
      font-size: .5em;
    }
  }
}

.header-contacts{
  margin-left: auto;
  display: flex;
  align-items: center;
  .socials{
    margin-left: 1.25em;
    &__item{
      width: 1.25em;
      height: 1.25em;
      background: #282828;
      @include lastM(0 .625em 0 0);
      &:hover{
        background: $yellow;
      }
      img{
        width: .625em;
      }
    }
  }
  &__phone{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .31em;
    }
  }
}

.header-bottom{
  background-color: $purple;
  color: #fff;
  &__inner{
    min-height: 5.1875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav2{
  .nav{
    &__hidden{
      color: $black;
    }
    &__link,
    &__item{
      @include lastM(0 4.06em 0 0);
    }
    &__link,
    &__show{
      font-weight: 700;
      span{
        font-size: 1em;
      }
      svg{
        display: none;
      }
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__item{
    position: relative;
    width: 1.25em;
    flex-shrink: 0;
    @include lastM(0 1.75em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $yellow;
      }
    }
  }
}

._pin{
  position: absolute;
  top: -.38em;
  right: -.5em;
  border-radius: 50%;
  @include center;
  width: 0.9375em;
  height: 0.9375em;
  font-weight: 500;
  background-color: #026317;
  span{
    font-size: 0.6875em;
  }
  &._red{
    background-color: #BA0303;
  }
}

.mob-links,
.header-feedback,
.header-search,
.menu__close,
.header__tablet-view{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    ._hide-mob{
      display: none;
    }
    &__logo{
      width: 6.9375em;
    }
    &__tablet-view{
      display: flex;
      flex-grow: 1;
      margin: 0 1.44em 0 2.94em;
      .header-search{
        flex-grow: 1;
        margin: 0 0 0 1.44em;
        height: 2.75em;
        input{
          height: 100%;
          font-size: .8125em;
          border-radius: .6em;
          padding: 0 5.5em 0 1em;
          border-color: #BDBDBD;
        }
        &__btn{
          right: 0;
          width: 4em;
          height: calc(100% - 2px);
          border-radius: 0 0.375em 0.375em 0;
          background: #F5F3F3;
          img{
            width: 1.8em;
          }
        }
      }
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
  }

  .header-top{
    display: none;
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 10;
    padding: .94em 0;
    color: $black;
    overflow-y: auto;
    transition: all .4s;
    transform: translateX(-120%);
    &__close{
      @include center;
      width: 1.25em;
      margin: 0 0 .94em .94em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .nav2{
    flex-direction: column;
    align-items: stretch;
    .nav{
      &__link,
      &__item{
        margin: 0 !important;
      }
      &__link,
      &__show{
        font-weight: 600;
        background-color: $purple;
        color: #fff;
        min-height: 2.5em;
        justify-content: space-between;
        padding: .25em .94em;
        svg{
          display: block;
          width: .625em;
        }
      }
      &__hidden{
        padding: .94em;
        display: none;
      }
      &__item{
        &._show{
          .nav__show svg{
            transform: rotate(-180deg);
          }
        }
      }
    }
  }

  .mob-links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: .94em;
    .nav__link{
      @include lastM(0 0 .75em);
    }
  }

  .header-feedback{
    padding: 1.5em .94em 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    &__item{
      border-radius: 0.625em;
      background: #F5F5F5;
      padding: .94em;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__phone{
      display: flex;
      align-items: center;
      font-weight: 700;
      margin-bottom: .625em;
      img{
        width: 1.125em;
        flex-shrink: 0;
        margin-right: .375em;
      }
    }
  }

  .header-search{
    display: block;
    margin: 0 auto 1.25em;
    width: calc(100% - 1.88em);
    input{
      font-size: .625em;
      height: 3.6em;
      padding: 0 2.5em 0 1.6em;
      border-radius: 1em;
    }
    &__btn{
      position: absolute;
      transform: translateY(-50%);
      top: 50%;
      right: .875em;
      width: 1em;
      @include center;
      background-color: transparent;
      img{
        width: 100%;
      }
    }
  }

  .catalog-btn{
    flex-shrink: 0;
    height: 2.75em;
    padding: 0 1.25em;
    font-weight: 700;
    svg{
      width: 1.0625em;
      height: auto;
      margin-right: .83em;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__tablet-view{
      order: -1;
      margin: 0;
      flex-grow: unset;
      .header-search{
        display: none;
      }
    }
    &__logo{
      width: 5em;
    }
  }

  .header-bottom{
    &__inner{
      min-height: 3.875em;
    }
  }

  .header-feedback{
    grid-template-columns: 100%;
  }

  .header-action{
    &__item{
      width: 1.125em;
      @include lastM(0 1.31em 0 0);
    }
  }

  ._pin{
    top: -.31em;
    right: -.44em;
    width: 0.6875em;
    height: 0.6875em;
    span{
      font-size: .5em;
    }
  }

  .catalog-btn{
    background-color: transparent !important;
    padding: 0;
    height: auto;
    svg{
      width: 1.125em;
      margin: 0;
    }
    path{
      fill: #fff;
    }
    span{
      display: none;
    }
  }
}
