.catalog{
  padding: 1.56em 0 3.12em;
  &__inner{
    display: grid;
    grid-template-columns: 20.25em calc(100% - 20.25em - .94em);
    gap: .94em;
  }
  .breadcrumbs{
    padding: 0 0 2.63em;
  }
  &__items{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .625em));
    gap: 1.875em .9375em;
  }
  &__text{
    margin-top: 2.5em;
  }
}

.filter{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .875em .94em;
    border-radius: 0.4375em;
    background-color: $yellow;
    min-height: 2em;
    position: relative;
    z-index: 2;
  }
  &__bottom{
    border-radius: .4375em;
    border: 1px solid #E8E8EE;
    margin-top: -2em;
    padding-top: 2em;
  }
  &__item{
    padding: 1.25em 1em;
    &:not(:last-child){
      border-bottom: 1px solid #E4E3E9;
    }
  }
  &__nav{
    @include grid100gap(1.25em);
    padding: 1.56em .94em .94em;
    .m-btn{
      font-weight: 600;
    }
  }
}

.filter-slider{
  &__item{
    min-height: .9375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
    margin-top: 1em;
  }
  .range-slider{
    margin: 0 auto;
    width: calc(100% - .25em);
    background: rgba(0, 0, 0, 0.20);
    border: none;
    box-shadow: none;
    height: .125em;
    border-radius: 0;
    cursor: pointer;
    .noUi-connect{
      background-color: $purple;
    }
    .noUi-handle{
      box-shadow: none;
      background-color: $purple;
      border: none;
      width: .625em;
      height: .625em;
      top: -.25em !important;
      right: -.3125em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    display: flex;
    align-items: center;
    &-split{
      flex-shrink: 0;
      margin: 0 .94em;
    }
  }
  .fg{
    flex-grow: 1;
  }
  &__range{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    margin: 0 0 .4em;
  }
}

.catalog-sort{
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__field{
    display: flex;
    align-items: center;
  }
  &__label{
    margin-right: .625em;
  }
  .nice-select{
    width: auto;
    height: auto;
    padding: 0;
    border: none;
    &:before{
      margin-left: .375em;
      width: .625em;
      height: .625em;
    }
  }
  .current{
    text-decoration: underline;
    font-size: 1em;
    line-height: 125%;
  }
}

.pagination{
  display: flex;
  align-items: center;
  margin-top: 2.5em;
  &__item,
  &__btn{
    font-weight: 500;
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:hover{
      color: $yellow;
    }
  }
  &__item{
    margin-right: .375em;
    height: 2.25em;
    min-width: 2.25em;
    @include center;
    padding: 0 .25em;
    border-radius: 0.4375em;
    &._active{
      background: #1C195E;
      color: #fff;
      pointer-events: none;
    }
  }
  &__btn{
    text-decoration: underline;
    &._next{
      margin-left: 1em;
    }
    &._prev{
      margin-right: 1.375em;
    }
  }
}

.not-found{
  background-color: $black;
  color: #fff;
  overflow: hidden;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/found-bg.webp);
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 110%;
  }
  &__inner{
    position: relative;
    z-index: 2;
    padding: 0 14em;
    display: flex;
    align-items: flex-end;
  }
  &__content{
    padding: 5.625em 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__btn{
    margin-top: 1.25em;
    padding: 0 2.12em;
    font-weight: 600;
  }
  &__img{
    width: 26em;
    margin-left: 4.31em;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding-bottom: 2.25em;
    &__inner{
      grid-template-columns: 100%;
    }
    .breadcrumbs{
      padding-bottom: 1em;
    }
    &__top{
      margin-bottom: 1em;
    }
    &__items,
    &__text{
      margin-top: 1em;
    }
  }

  .filter{
    &._active{
      .filter__toggle{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &__bottom{
      display: none;
    }
  }

  .catalog-sort{
    margin: 0;
  }

  .pagination{
    margin-top: 1em;
  }

  .not-found{
    &__inner{
      padding: 0;
      display: block;
    }
    &__content{
      padding: 2.25em 0;
    }
    &__img{
      display: none;
    }
    &__btn{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    .product-card{
      font-size: .625em;
    }
  }

  .catalog-sort{
    flex-direction: column;
    align-items: flex-start;
    &__field{
      margin-top: .75em;
      font-size: .75em;
    }
  }

  .pagination{
    &__btn{
      display: none;
    }
  }

  .not-found{
    &__btn{
      padding: 0;
      width: 100%;
      span{
        font-size: .875em;
      }
    }
  }
}
