.main{
  position: relative;
  padding: 5.13em 0 3.75em;
  color: #FFF;
  &:before{
    z-index: 2;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }
  &__content{
    position: relative;
    z-index: 3;
  }
  &__text{
    @include grid100gap(.94em);
    max-width: 41em;
  }
  &__items{
    display: flex;
    margin-top: 8.56em;
  }
  &__item{
    display: flex;
    max-width: 9.125em;
    @include lastM(0 4.19em 0 0);
    &-icon{
      flex-shrink: 0;
      margin-right: .75em;
      width: 1.25em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.categories{
  padding: 3.125em 0 1.5625em;
  background: #F5F5F5;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .625em));
    gap: .9375em;
  }
}

.category-card{
  border-radius: 0.4375em;
  background: #FFF;
  padding: 1.25em 7em 1.25em 1.25em;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  &._type2{
    background: #E4E3E9;
    &:before{
      background-color: rgba(248, 165, 26, 0.28);
    }
  }
  &:before{
    content: '';
    position: absolute;
    width: 13.0625em;
    height: 13.0625em;
    background-color: rgba(28, 25, 94, 0.10);
    top: -5.94em;
    right: -4.88em;
    border-radius: 50%;
  }
  &__icon{
    position: absolute;
    top: 1.25em;
    right: 1.875em;
    height: 5em;
    display: block;
    z-index: 2;
  }
  &__text{
    margin-top: 1.875em;
  }
  &__list{
    @include grid100gap(.38em);
    li{
      position: relative;
      padding-left: 1.375em;
      &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 1em;
        height: 1em;
        background-image: url(../img/arrow.svg);
        @include bgImgSize(100%);
      }
      span{
        font-size: .875em;
        line-height: 121%;
      }
    }
  }
  &__nav{
    padding-top: 1.94em;
    margin-top: auto;
    display: flex;
  }
}

.selection,
.filter{
  &__toggle{
    width: 1em;
    height: 1em;
    position: relative;
    img{
      @include centerAbsolute;
      width: 100%;
      display: block;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.selection{
  padding: 1.5625em 0 3.125em;
  background: #F5F5F5;
  &__inner{
    padding: 1.875em;
    background-color: #fff;
    border-radius: 0.4375em;
  }
  &__toggle{
    display: none;
  }
  &__bottom{
    margin-top: 1.56em;
    @media screen and (min-width: 577px){
      display: block !important;
    }
  }
}

.selection-filter{
  &__types{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
  &__fields{
    margin-top: 2.58em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 1.94em * 3 / 4));
    gap: 1.88em 1.94em;
  }
  &__price{
    grid-column: span 2;
    &-fields{
      display: flex;
      align-items: center;
    }
    &-field{
      position: relative;
      width: 10em;
      input{
        padding-left: 2.75em;
      }
    }
    &-split{
      margin: 0 1.56em;
      font-weight: 500;
    }
    &-title{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 1em;
      font-weight: 500;
      pointer-events: none;
    }
  }
  &__nav{
    grid-column: span 2;
    max-width: 28.125em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.875em / 2));
    gap: 1.875em;
    .m-btn{
      padding: 0;
      font-weight: 600;
    }
  }
}

.type-input{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: .94em 1em 1.42em;
  input:checked~label{
    border-color: $yellow;
    &:after{
      border-color: $yellow;
      background-size: .5em auto;
    }
  }
  label{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    pointer-events: none;
    border-radius: 0.4375em;
    border: 1px solid $purple;
    transition: all .1s;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%,-50%);
    }
    &:before{
      background-color: #fff;
      width: 2.9375em;
      height: 4px;
    }
    &:after{
      width: 1.5em;
      height: 1.5em;
      border: 2px solid $purple;
      border-radius: 50%;
      background-image: url(../img/check2.svg);
      @include bgImgSize(0);
    }
  }
  &__img{
    height: 7.125em;
    display: flex;
    justify-content: center;
    margin-bottom: .94em;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 2.62em 0 3.06em;
    &__text{
      gap: .88em;
      max-width: 32.625em;
    }
    .text20{
      line-height: 150%;
    }
    &__items{
      margin-top: 1.63em;
    }
    &__item{
      max-width: 7.875em;
      @include lastM(0 5.37em 0 0);
      &-icon{
        margin-right: .625em;
        width: .875em;
      }
    }
  }

  .categories{
    padding: 1.625em 0 .75em;
  }

  .category-card{
    padding: .625em;
    &:before{
      right: -9.07em;
      top: -8.69em;
    }
    &__top{
      min-height: 2.75em;
      padding-right: 6em;
    }
    &__icon{
      height: 2.61em;
      top: .625em;
      right: .625em;
    }
    &__text{
      margin-top: 1.44em;
    }

    @media screen and (min-width: 577px){
      .text14{
        font-size: .75em;
        line-height: 125%;
      }
      &__list{
        li{
          padding-left: 1.1875em;
          &:before{
            height: .9375em;
            width: .9375em;
            top: 50%;
            transform: translateY(-50%);
          }
          span{
            font-size: .75em;
            line-height: 125%;
          }
        }
      }
    }
    &__nav{
      padding-top: 2.69em;
      .m-btn{
        width: 100%;
        padding: 0 1em;
        span{
          font-size: 0.8125em;
        }
      }
    }
  }

  .selection{
    padding: .75em 0 1.81em;
    &__inner{
      padding: 1.38em 1em 1.12em;
    }
    &__bottom{
      margin-top: 1.45em;
    }
  }

  .selection-filter{
    &__types{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.28em / 3));
      gap: .64em;
    }
    &__fields{
      margin-top: 1.69em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1.28em / 3));
      gap: 1.25em .64em;
    }
    &__price{
      &-field{
        width: 9.6875em;
      }
      &-split{
        margin: 0 .56em;
      }
    }
    &__nav{
      grid-column: unset;
      width: calc(100% + 6em);
      max-width: unset;
      margin-left: -6em;
      grid-template-columns: repeat(2,calc(50% - .78em));
      gap: 1.56em;
      align-self: flex-end;
      .m-btn{
        height: 2.5em;
      }
    }
  }

  .type-input{
    padding: 1.13em 1em .72em;
    &__img{
      height: 4.25em;
      margin-bottom: .2em;
    }
    input:checked~label{
      &:after{
        background-size: .375em auto;
      }
    }
    label{
      &:before{
        width: 1.85em;
      }
      &:after{
        width: .9375em;
        height: .9375em;
        border-width: 1px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    display: none;
  }

  .categories{
    padding: 2.5em 0 1.25em;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .category-card{
    padding: .94em;
    min-height: 15.6875em;
    &:before{
      top: -8em;
      right: -8.38em;
    }
    &__icon{
      height: 3.125em;
      top: .94em;
      right: .94em;
    }
    &__text{
      margin-top: 1.12em;
    }
  }

  .selection{
    padding: 1.25em 0 2.5em;
    &._active{
      .selection__toggle{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    &__inner{
      padding: 0;
    }
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: .875em .94em;
      border-radius: 0.4375em;
      background-color: $yellow;
    }
    &__toggle{
      display: flex;
    }
    &__bottom{
      margin-top: 1.25em;
      padding: 0 .625em .625em;
      display: none;
    }
  }

  .selection-filter{
    &__types{
      grid-template-columns: repeat(3,calc(100% / 3 - 1.28em / 3));
      gap: .64em;
    }
    &__fields{
      margin-top: 1.625em;
      grid-template-columns: 100%;
    }
    &__price{
      grid-column: 1/-1;
      &-field{
        width: auto;
        flex-grow: 1;
      }
      &-split{
        margin: 0 .625em;
      }
    }
    &__nav{
      width: 100%;
      margin: 1.25em 0 0;
      grid-template-columns: 100%;
      gap: 1.25em;
      .m-btn{
        height: 3em;
      }
    }
  }

  .type-input{
    padding: .25em .25em .7em;
    label{
      border-radius: .3125em;
      &:before{
        width: 1.375em;
      }
      &:after{
        width: .875em;
        height: .875em;
      }
    }
    &__img{
      margin: 0;
      height: 1.7em;
    }
    &__title{
      display: none;
    }
  }
}
