.fg {
  position: relative;
  margin: 0;
  label{
    font-size: .875em;
    line-height: 129%;
    display: block;
    color: $gray;
    font-weight: 500;
    margin-bottom: .33em;
  }
  input {
    border: 1px solid rgba(0, 0, 0, 0.50);
    padding: 0 1.14em;
    height: 2.85em;
    width: 100%;
    font-size: .875em;
    background-color: #fff;
    color: $black;
    border-radius: .5em;
    &::placeholder {
      color: $gray;
    }
    &:focus {
      border-color: $yellow;
    }
  }
}

.fg{
  .nice-select{
    border: 1px solid rgba(0, 0, 0, 0.50);
    font-size: 1em;
    width: 100%;
    padding: 0 1em;
    height: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:focus{
      border-color: $yellow;
    }
    &:before{
      content: '';
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      margin-left: 1em;
      order: 3;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(100%);
    }
  }
  .current{
    font-size: .875em;
    line-height: 120%;
  }
  .list{
    border-radius: .4375em;
    width: auto;
    min-width: 100%;
  }
  .option{
    padding: 0 1.14em;
    font-size: .875em;
    height: 2em;
    display: flex;
    align-items: center;
  }
}

.m-check,
.type-input{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check {

  input {

    &:checked+label {
      &::before {
        border-color: $purple;
        background-size: .5625em auto;
      }
    }
  }
  label {
    padding-left: 1.31em;
    position: relative;
    min-height: 1em;
    cursor: pointer;
    color: $black;
    font-weight: 500;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 0.25em;
      border: 1px solid $gray;
      width: 1em;
      height: 1em;
      background-image: url(../img/check3.svg);
      @include bgImgSize(0);
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
}
