.collection{
  padding: 3.125em 0;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .swiper{
    &-nav{
      display: flex;
      align-items: center;
    }
    &-button{
      position: static;
      margin: 0 0 0 1.25em;
    }
  }
}

.collection-slider{
  margin-top: 1.56em;
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + .94em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      width: 25%;
      padding-right: .94em;
    }
  }
}

.product-card{
  border-radius: 0.625em;
  border: 1px solid #343434;
  padding: 2.5em 1.13em 1.31em;
  transition: all .3s;
  display: flex;
  flex-direction: column;
  height: auto;
  position: relative;
  &:hover{
    border-color: $yellow;
  }
  &__badge{
    position: absolute;
    top: -1px;
    right: -1px;
    border-radius: 0 0.625em;
    background: #F81A1A;
    @include center;
    height: 2.5em;
    min-width: 5em;
    padding: 0 1em;
    color: #fff;
    font-weight: 700;
  }
  &__img{
    height: 12.0625em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      transition: all .3s;
    }
    &:hover{
      img{
        transform: scale(1.05);
      }
    }
  }
  &__content{
    margin-top: .94em;
  }
  &__title{
    line-height: 150%;
    font-weight: 600;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
    margin-top: .47em;
  }
  &__tag{
    margin: .47em .94em 0 0;
    padding: 0 .9375em;
    border-radius: 0.25em;
    background: #F5F5F5;
    height: 1.75em;
    display: flex;
    align-items: center;
    span{
      font-size: .75em;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__prices{
    position: relative;
  }
  &__price{
    font-size: 1.5em;
    line-height: 125%;
    font-weight: 700;
  }
  &__price2{
    position: absolute;
    bottom: 100%;
    left: 0;
    margin-bottom: .12em;
    color: #999;
    text-decoration: line-through;
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__btn{
    height: 2.5em;
    width: 10em;
    text-transform: uppercase;
    font-weight: 500;
  }
}

.services{
  padding-bottom: 3.125em;
  &__grid{
    margin-top: 1.56em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
  @media screen and (min-width: 993px){
    .service-card{
      &._big{
        grid-column: 1/-1;
      }
    }
  }
}

.service-card{
  padding: 1.875em 1em 1.875em 2.15em;
  display: flex;
  flex-direction: column;
  min-height: 17.375em;
  border-radius: 0.4375em;
  background-size: cover;
  background-position: center;
  color: #fff;
  &:before{
    background: linear-gradient(0deg, rgba(5, 5, 15, 0.70) 0%, rgba(5, 5, 15, 0.70) 100%);
  }
  &__text{
    position: relative;
    z-index: 2;
    max-width: 30em;
    @include grid100gap(1em);
    .text22{
      text-transform: uppercase;
    }
    .text16{
      line-height: 150%;
    }
    a{
      text-decoration: underline;
    }
  }
  &__nav{
    position: relative;
    z-index: 2;
    margin-top: auto;
    display: flex;
    padding-top: 1.37em;
    .m-btn{
      font-weight: 600;
      padding: 0 1.38em;
      min-width: 13.125em;
      max-width: 100%;
    }
  }
}

.shops{
  position: relative;
  min-height: 42.375em;
  display: flex;
  align-items: center;
  .map{
    width: 50%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__title{
    display: none;
  }
}

.shops-info{
  padding: 1.875em 1.875em 2.625em;
  border-radius: 0.625em 0 0 0.625em;
  border: 1px solid #E8E8EE;
  background: #FFF;
  position: relative;
  z-index: 2;
  width: calc(50% + 10em);
  margin-left: auto;
  &__text{
    margin-top: 1.56em;
    font-weight: 500;
    max-width: 45.5em;
    p{
      font-size: 1.0625em;
      line-height: 159%;
      @include lastM(0 0 1.5em);
    }
  }
  &__toggle-text{
    display: none;
  }
}

.buy-now{
  margin-top: 1.875em;
  &__content{
    display: inline-block;
    padding: .94em 1.06em 1.25em;
    border-radius: 0.4375em;
    background: #F5F5F5;
    margin-top: .94em;
  }
  &__phone{
    display: inline-flex;
    align-items: center;
    margin-top: .94em;
    img{
      width: 1.31em;
      flex-shrink: 0;
      margin-right: .44em;
    }
  }
}

.advantages{
  padding: 6.25em 0;
  background: #F5F5F5;
  overflow: hidden;
  .text30{
    text-align: center;
  }
  &__items{
    margin-top: 1.56em;
    margin-left: -1em;
    display: flex;
    justify-content: center;
    width: calc(100% + 5em);
  }
}

.advantage-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 2.5em;
  text-align: center;
  &__icon{
    height: 5em;
    display: block;
    margin-bottom: .94em;
  }
  &__text{
    max-width: 12.75em;
    a{
      font-size: .875em;
      line-height: 120%;
      display: block;
      margin-top: .35em;
      font-weight: 500;
      text-decoration: underline;
      color: $purple;
    }
  }
}

.req{
  overflow: hidden;
  padding: 6.25em 0;
  background: #282828;
  color: #fff;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 45.8%;
    &:before{
      background: linear-gradient(0deg, rgba(40, 40, 40, 0.70) 0%, rgba(40, 40, 40, 0.70) 100%);
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__img2{
    position: absolute;
    right: 24.8%;
    bottom: 0;
    width: 34.3%;
  }
}

.req-form{
  max-width: 32em;
  &__text{
    margin-top: 1.19em;
  }
  &__grid{
    margin-top: .94em;
    display: grid;
    grid-template-columns: calc(100% - 15em) 13.125em;
    gap: .81em 1.875em;
  }
  .fg{
    input{
      background-color: #fff;
      border-color: #fff;
      height: 3.43em;
      font-weight: 600;
    }
  }
  .m-btn{
    font-weight: 600;
  }
  &__hint{
    margin-top: .5em;
    a{
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){
  .collection{
    padding: 1.625em 0;
    .swiper{
      &-button{
        margin-left: .9375em;
      }
    }
  }

  .collection-slider{
    margin-top: 1.75em;
    .swiper{
      &-slide{
        width: calc(100% / 3);
      }
    }
  }

  .product-card{
    padding: 1.31em .625em .625em;
    &__img{
      height: 8.38em;
    }
    &__badge{
      min-width: 4.92856em;
      height: 2em;
    }
    &__title{
      font-size: .75em;
      letter-spacing: 0.015em;
    }
    &__bottom{
      padding-top: .75em;
      display: block;
    }
    &__prices{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__price2{
      position: static;
      order: 2;
      font-weight: 700;
      span{
        font-size: .875em;
      }
    }
    &__btn{
      width: 100%;
      margin-top: .94em;
    }
  }

  .services{
    padding: 1.625em 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
    @media screen and (min-width: 577px){
      .service-card{
        &._tablet-small{
          width: calc(100% - 7.875em);
          .text22{
            word-break: break-word;
          }
          .service-card__nav .m-btn{
            width: 100%;
            min-width: unset;
          }
        }
        &._tablet-big{
          width: calc(100% + 7.875em);
          margin-left: -7.875em;
        }
      }
    }
  }

  .service-card{
    padding: 1.25em;
    &__text{
      gap: .625em;
    }
    &__nav{
      padding-top: 1.875em;
    }
  }

  .shops{
    padding: 1.5625em 0;
    min-height: unset;
    display: block;
    .map{
      margin-top: 1.56em;
      width: 100%;
      height: 34.5em;
      position: static;
    }
    &__title{
      display: block;
      text-align: center;
    }
  }

  .shops-info{
    width: 100%;
    margin: 0 0 3.31em;
    padding: 0 .94em;
    border: none;
    .text30{
      max-width: 50%;
      font-size: 1.5em;
    }
    &__text{
      column-count: 2;
      column-gap: 1.44em;
    }
  }

  .buy-now{
    margin-top: 3em;
    &__title{
      display: none;
    }
    &__content{
      max-width: 29.1875em;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .text14{
        font-size: .75em;
        max-width: 14em;
      }
    }
    &__phone{
      white-space: nowrap;
      margin: 0;
      .text22{
        font-size: 1.25em;
      }
      img{
        width: 1.125em;
        margin-right: .38em;
      }
    }
  }

  .advantages{
    padding: 3.75em 0;
    &__items{
      margin-top: -1.56em;
      margin-left: 0;
      width: calc(100% + 3.125em);
      flex-wrap: wrap;
    }
  }

  .advantage-item{
    margin: 3.125em 3.125em 0 0;
    width: calc(100% / 3 - 3.125em);
  }

  .req{
    padding: 1.875em 0 2.44em;
    &__img{
      width: 100%;
    }
    &__img2{
      width: 29.5em;
      right: -5.38em;
    }
  }

  .req-form{
    position: relative;
    z-index: 3;
    max-width: 19.75em;
    &__text{
      margin-top: .75em;
    }
    &__grid{
      margin-top: .625em;
      grid-template-columns: 100%;
      max-width: 16.875em;
    }
    &__hint{
      max-width: 19.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .collection{
    padding: 2.5em 0 1.25em;
  }

  .collection-slider{
    margin-top: 1.25em;
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .product-card{
    &__title{
      max-width: 90%;
      display: inline-block;
    }
  }

  .services{
    padding: 1.25em 0;
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }

  .service-card{
    padding: .9375em;
    &__nav{
      .m-btn{
        padding: 0;
        width: 100%;
        min-width: unset;
      }
    }
  }

  .shops{
    padding: 1.25em 0 2.5em;
    &__title,
    .map{
      display: none;
    }
  }

  .shops-info{
    margin: 0;
    .text30{
      max-width: 70%;
      font-size: 1.375em;
    }
    &__text{
      column-count: 1;
      margin-top: .94em;
      p{
        @include lastM(0 0 .75em);
      }
    }
    &:not(._show-all-text){
      .shops-info__text p:nth-child(1)~*{
        display: none;
      }
      .shops-info__toggle-text{
        display: inline-flex;
      }
    }
    &__toggle-text{
      margin-top: 1.75em;
      text-decoration-line: underline;
      font-weight: 600;
      &:hover{
        text-decoration: none;
      }
      span{
        font-size: .875em;
        line-height: 143%;
      }
    }
  }

  .buy-now{
    margin-top: 2.5em;
    &__content{
      display: block;
    }
    &__phone{
      margin: .625em 0 0;
    }
  }

  .advantages{
    padding: 2.5em 0;
    &__items{
      margin-top: -.31em;
      width: calc(100% + 1em);
    }
  }

  .advantage-item{
    margin: 2.19em 1em 0 0;
    width: calc(50% - 1em);
    &__icon{
      height: 3.125em;
      margin-bottom: .625em;
    }
    &__text{
      font-size: .875em;
      max-width: unset;
    }
  }

  .req{
    padding: 2.81em 0;
    &__img2{
      display: none;
    }
  }

  .req-form{
    &__grid{
      max-width: 100%;
    }
  }
}
