.catalog-item{
  padding: 2.625em 0 3.125em;
  &__inner{
    display: grid;
    grid-template-columns: 38.5em calc(100% - 38.5em - 3.94em);
    gap: 3.94em;
  }
  &__top{
    padding-left: 5.125em;
    padding-right: 3em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__buttons{
    @include center;
    margin-top: 1.81em;
  }
  &__btn{
    @include lastM(0 4em 0 0);
    display: flex;
    align-items: center;
    svg{
      width: 1.5em;
      flex-shrink: 0;
      height: auto;
      margin-right: .36em;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
    &:hover{
      color: $yellow;
      path{
        fill: $yellow;
      }
    }
  }
  &__title{
    max-width: 95%;
  }
  &__info{
    margin-top: 1.31em;
    max-width: 22.125em;
    &-top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  &__brand{
    width: 5em;
    flex-shrink: 0;
  }
  &__prices{
    margin-top: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price{
    font-size: 2.125em;
    line-height: 125%;
  }
  &__buy{
    width: 12em;
    text-transform: uppercase;
    font-weight: 600;
    img{
      width: 1.5625em;
      margin-right: .44em;
    }
    span{
      font-size: 1.125em;
    }
  }
  &__wrap{
    display: flex;
    align-items: center;
    margin-top: 2.63em;
    .product-available__item{
      width: 14em;
    }
    .catalog-item__buttons{
      justify-content: unset;
      margin: 0 0 0 3.125em;
    }
    .catalog-item__btn{
      @include lastM(0 3.125em 0 0);
      svg{
        width: 1.25em;
      }
    }
  }
  &._type2{
    @media screen and (min-width: 993px){
      .catalog-item__img{
        max-width: 25.81em;
        margin-left: auto;
      }
      .catalog-info{
        margin-top: 1.31em;
      }
    }
  }
}

.catalog-nums{
  padding-left: 4.93em;
  margin-top: 1.31em;
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 2.75em / 3));
  gap: 1.375em;
  &__item{
    border-radius: 0.4375em;
    border: 1px solid #E8E8EE;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 1.25em .25em;
  }
  &__title{
    font-weight: 600;
    color: $purple;
  }
  &__desc{
    margin-top: .44em;
  }
}

.change{
  margin-top: 2.5em;
  padding: 1.25em 1em;
  border-radius: 0.4375em;
  background: #00B007;
  display: flex;
  align-items: center;
  color: #fff;
  justify-content: space-between;
  &__wrap{
    display: flex;
    align-items: center;
    max-width: 13em;
    img{
      width: 1.875em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    a{
      text-decoration: underline;
    }
  }
}

.product-available{
  margin-top: 1.25em;
  max-width: 31.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .125em));
  gap: .25em;
  &__item{
    padding: .94em;
    text-align: center;
    border-radius: 0.4375em;
    border: 1px solid #F5F5F5;
    b{
      font-weight: 600;
      text-decoration: underline;
    }
    &._gray{
      background: #F5F5F5;
    }
  }
}

.worker{
  padding: 1.5em 1.44em .875em;
  border-radius: 0.4375em;
  background: #F5F5F5;
  margin-top: 1.25em;
  display: flex;
  align-items: center;
  &__img{
    width: 6.5625em;
    height: 6.5625em;
    flex-shrink: 0;
    margin-right: 1.13em;
    img{
      display: block;
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.worker-form{
  margin-top: .94em;
  display: grid;
  grid-template-columns: calc(100% - 10.4375em - .94em) 10.4375em;
  gap: .94em;
  .fg{
    height: 2.75em;
    input{
      height: 100%;
      border-color: #fff;
    }
  }
  .m-btn{
    height: 2.75em;
    font-weight: 600;
    text-transform: uppercase;
  }
}

.catalog-info{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: calc(100% - 48.5em - 1.875em) 28.3em 20.2em;
  gap: .9375em;
  &__text{
    grid-column: 1/3;
  }
}

.props{
  padding: 1.25em 1.46em;
  border-radius: 0.4375em;
  border: 1px solid #E8E8EE;
  &__items{
    @include grid100gap(.75em);
    margin-top: 1.25em;
  }
  &__addresses{
    @include grid100gap(1.0625em);
    margin-top: 1.25em;
  }
  &._type2{
    .prop-item{
      grid-template-columns: 2.5fr 1fr;
    }
  }
  &._type3{
    margin-top: 2.5em;
    .prop-item{
     max-width: 24em;
    }
  }
}

.prop-item{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: .25em 1.46em;
  &__title,
  &__desc{
    font-size: 1em;
    line-height: 125%;
  }
  &__desc{
    font-weight: 600;
  }
}

.address-item{
  display: flex;
  &__icon{
    flex-shrink: 0;
    width: 1.2em;
    margin-right: .37em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    .text12{
      line-height: 175%;
    }
  }
}

.req2{
  background: linear-gradient(101deg, #E4E3E9 55.66%, #D9D9D9 55.66%, #1C195E 55.75%, #1C195E 55.77%, #1C195E 55.88%);
  color: #343434;
  .req-form{
    .m-btn{
      padding: 0;
      text-transform: uppercase;
    }
    &__hint{
      max-width: 18em;
    }
  }
  .req__img2{
    bottom: 3em;
  }
}

.catalog-text{
  padding: 3.125em 0 0;
  .text{
    p{
      @include lastM(0 0 2.5em);
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding: 1.25em 0;
    &__inner{
      margin-top: 1em;
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__top{
      padding: 0;
    }
    &__img{
      max-width: 25em;
      margin: 0 auto;
    }
  }

  .catalog-nums{
    padding: 0;
  }

  .catalog-info{
    margin-top: 1.25em;
    grid-template-columns: 100%;
    &__text{
      grid-column: 1/-1;
    }
  }

  .catalog-text{
    padding: 1.625em 0;
    .text{
      p{
        @include lastM(0 0 1.25em);
      }
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__top{
      padding: 0;
    }
    &__buttons{
      justify-content: space-between;
    }
    &__btn{
      @include lastM(0 1em 0 0);
      svg{
        width: 1.125em;
      }
    }
    &__info{
      margin: 0;
      &-top{
        flex-direction: column;
        align-items: flex-start;
      }
    }
    &__brand{
      margin: .5em 0 0;
    }
    &__prices{
      flex-direction: column;
      align-items: flex-start;
      margin-top: 1em;
    }
    &__buy{
      margin-top: .25em;
      width: 100%;
    }
    &__wrap{
      margin-top: 1.5em;
      display: block;
      .product-available__item{
        width: 100%;
      }
      .catalog-item__buttons{
        margin: 1em 0 0;
      }
      .catalog-item__btn{
        @include lastM(0 1em 0 0);
        svg{
          width: 1.125em;
        }
      }
    }
  }

  .catalog-nums{
    grid-template-columns: 100%;
    gap: .75em;
    padding: 0;
    &__item{
      padding: .75em .25em;
    }
    &__title{
      font-size: 1.125em;
    }
    &__desc{
      font-size: .875em;
    }
  }

  .change{
    margin-top: 1em;
    flex-direction: column;
    align-items: flex-start;
    padding: .75em;
    &__wrap{
      margin: .5em 0 0;
      max-width: 100%;
    }
  }

  .product-available{
    grid-template-columns: 100%;
  }

  .worker{
    padding: .625em;
    flex-direction: column;
    text-align: center;
    &__img{
      margin: 0 0 .625em;
    }
  }

  .worker-form{
    grid-template-columns: 100%;
  }

  .props{
    padding: .625em;
    .text22{
      font-size: 1.125em;
    }
    &__items{
      margin-top: 1em;
    }
    &__addresses{
      margin-top: 1em;
      gap: .5em;
    }
    &._type3{
      margin-top: 1.25em;
    }
  }

  .prop-item{
    grid-template-columns: 100%;
    &__title,
    &__desc{
      font-size: .875em;
    }
  }
}
