@import "../vars.scss";
.breadcrumbs {
    padding: 1.25em 0;
    &__item {
        position: relative;
        font-weight: 300;
        display: inline;
        font-size: .6875em;
        line-height: 125%;
        &:not(:last-child){
            text-decoration: underline;
        }
        &:not(:first-child) {
            margin-left: 1.875em;
            &:before {
                content: '/';
                text-align: center;
                margin-right: 1em;
                //width: 2em;
                height: 1.25em;
                @include center;
                position: absolute;
                z-index: 1;
                top: .125em;
                right: 100%;
            }
        }
    }
}
