.footer{
  background: #1C195E;
  color: #fff;
  &__logo{
    display: block;
    width: 5.625em;
    flex-shrink: 0;
    margin-right: 8.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}


.footer-top{
  &__inner{
    display: flex;
    padding: 2.5em 0 1.75em;
  }
}

.footer-nav{
  margin-right: 3.75em;
  &__links{
    margin-top: .75em;
    max-width: 13em;
    &._col2{
      max-width: 16em;
      column-count: 2;
      column-gap: 2em;
    }
  }
  &__link{
    display: inline-flex;
    width: 100%;
    @include lastM(0 0 .75em);
    span{
      font-size: .75em;
      line-height: 125%;
    }
  }
}

.footer-contacts{
  margin-left: auto;
  &__bottom{
    margin-top: .625em;
  }
  &__btn{
    width: 100%;
    height: 2.75em;
    font-weight: 500;
    img{
      width: .9375em;
      margin-right: .37em;
    }
  }
  .socials{
    margin-top: .94em;
    justify-content: flex-end;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    @include lastM(0 .9375em 0 0);
    @include center;
    width: 1.875em;
    height: 1.875em;
    border-radius: 50%;
    background: #605E8E;
    &:hover{
      background: darken(#605E8E,10%);
    }
    img{
      width: .9375em;
    }
  }
}

.footer-places{
  border-top: 1px solid rgba(245, 245, 245, 0.30);
  &__inner{
    padding: 1.19em 4.69em .81em 0;
    display: flex;
    justify-content: space-between;
  }
}

.footer-office{
  padding: .75em .81em;
  max-width: 17.1875em;
  display: flex;
  border-radius: 0.75em;
  border: 1px solid transparent;
  &:hover,
  &._active{
    border-color: $yellow;
  }
  &__icon{
    width: 1em;
    margin-right: .625em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    display: inline-block;
    margin-top: .75em;
  }
  &__time{
    margin-top: .75em;
  }
}

.footer-info{
  padding: 1.12em 0;
  background-color: $black;
  color: rgba(255, 255, 255, 0.50);
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__logo{
      display: none;
    }
  }

  .footer-top{
    &__inner{
      padding: 1.88em 0 1.69em;
      display: grid;
      grid-template-columns: 1fr 1fr 1.2fr;
      gap: 1.875em;
      position: relative;
    }
  }

  .footer-contacts{
    @media screen and (min-width: 577px){
      right: 0;
      top: 100%;
      position: absolute;
      width: 13em;
      margin-top: 1.875em;
      text-align: right;
      .text22{
      }
      &__btn{
        margin-left: auto;
        padding: 0;
        width: 11.9em;
      }
    }
  }

  .footer-nav{
    margin: 0;
    &__links{
      &._col2{
        column-gap: 1.81em;
      }
    }
  }

  .footer-places{
    &__inner{
      margin-left: -.75em;
      padding: 1.5em 0 1.81em;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 2.3em .5em;
      max-width: 30em;
    }
  }

  .footer-office{
    &__address{
      font-size: .6875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer-places{
    display: none;
  }

  .footer-top{
    &__inner{
      grid-template-columns: 100%;
      padding: 2.5em 0;
    }
  }

  .footer-contacts{
    order: -1;
    width: calc(100% + 1.875em);
    margin-left: -.9375em;
    padding: 0 .9375em 1.75em;
    border-bottom: 1px solid rgba(245, 245, 245, 0.30);
    &__bottom{
      display: flex;
      align-items: center;
    }
    .socials{
      margin: 0 0 0 1.12em;
      flex-shrink: 0;
    }
    &__btn{
      padding: 0;
      flex-grow: 1;
      span{
        font-size: .75em;
      }
    }
  }

  .socials{
    &__item{
      width: 2em;
      height: 2em;
      @include lastM(0 1.25em 0 0);
    }
  }

  .footer-nav{
    &__link{
      @include lastM(0 0 .44em);
    }
  }

  .footer-info{
    &__conf{
      display: none;
    }
  }
}
