@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.m-backdrop{
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background: rgba(0, 0, 0, 0.45);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
  }
}

.swiper-button{
  width: 2.5625em;
  height: auto;
  &:hover{
    path{
      fill: $yellow;
    }
  }
  svg{
    width: 100%;
    height: auto;
  }
}


@media screen and (max-width: 992px){
  .swiper-button{
    width: 1.875em;
  }
}
