@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.875em;
    height: 3em;
    border-radius: 0.4375em;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-yellow{
    background-color: $yellow;
    color: $black;
    &:hover{
        background-color: darken($yellow,10%);
        color: $black;
    }
}

.m-btn-purple{
    background-color: $purple;
    color: #fff;
    &:hover{
        background-color: darken($purple,10%);
        color: #fff;
    }
}

.m-btn-green{
    background-color: #026317;
    color: #fff;
    &:hover{
        background-color: darken(#026317,10%);
        color: #fff;
    }
}

.m-btn-purple-outline{
    border: 1px solid $purple;
    color: $purple;
    &:hover{
        background-color: $purple;
        color: #fff;
    }
}
