.old-akb{
  background: linear-gradient(180deg, #D1D1D1 0%, #D6D6D6 38.56%, #D4D4D4 45.31%, #DBDBDB 81.25%, #DEDEDE 86.98%, #DEDEDE 96.76%);
  overflow: hidden;
  &__inner{
    display: flex;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
    }
    &:before{
      background: linear-gradient(90deg, rgba(176, 176, 176, 0.00) 21.81%, #A3A3A3 79.03%);
      backdrop-filter: blur(2px);
      width: 37.375em;
    }
    &:after{
      width: 2000px;
      left: 100%;
      background: #A4A4A4;
      z-index: 2;
    }
  }
  &__content{
    max-width: 28em;
    padding: .75em 0 2.69em;
    flex-shrink: 0;
  }

  .breadcrumbs{
    padding: 0 0 2.375em;
  }
  &__title{
    font-size: 2.25em;
    line-height: 125%;
    color: #1C195E;
  }
  &__nav{
    margin-top: 1.81em;
    display: flex;
    .m-btn{
      padding: 0 1em;
      text-transform: uppercase;
      font-weight: 600;
      img{
        margin-left: .31em;
        width: .9375em;
        flex-shrink: 0;
      }
    }
  }
  &__hint{
    display: flex;
    align-items: center;
    margin-top: 1.63em;
    color: #464646;
    img{
      width: 1.6875em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__img{
    width: 42.25em;
    display: block;
  }
}

.categories2{
  @media screen and (min-width: 993px){
    padding: 3.75em 0 2.125em;
    .category-card{
      min-height: 12.1875em;
    }
  }
}

.faq{
  padding: 3.75em 0;
  .text30{
    text-align: center;
  }
  &__items{
    max-width: 69.375em;
    margin: 1.875em auto 0;
    @include grid100gap(1.875em);
  }
}

.qa-card{
  &._active{
    ._view1{
      opacity: 0;
    }
    ._view2{
      opacity: 1;
    }
  }
  &__show{
    padding: 1.25em 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.4375em;
    background: #F5F5F5;
  }
  &__btn{
    width: 1.25em;
    height: 1.25em;
    position: relative;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      @include centerAbsolute;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
    &:hover{
      path{
        fill: $yellow;
      }
    }
  }
  &__title{
    max-width: 35em;
    .text18{
      line-height: 150%;
    }
  }
  &__hidden{
    padding: 1.25em 1.5em .81em;
    display: none;
  }
}

.become-client{
  background: #1C195E;
  color: #fff;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    object-fit: cover;
  }
  &__content{
    width: 50%;
    margin-left: auto;
    padding: 5.13em 0 5.13em 5.13em;
  }
  &__phone{
    margin: 1.25em 0 1.12em;
    a{
      font-size: 2.625em;
      font-weight: 700;
      line-height: 125%;
    }
  }
  &__text{
    margin-top: .31em;
  }
  &__links{
    max-width: 26.875em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    margin-top: 1.25em;
  }
  &__link{
    width: 100%;
    height: 3.5em;
    color: #fff;
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &._whatsapp{
      background: #026317;
      color: #fff;
      &:hover{
        background: darken(#026317,10%);
        color: #fff;
      }
    }
    &._tg{
      background: #0B84C8;
      color: #fff;
      &:hover{
        background: darken(#0B84C8,10%);
        color: #fff;
      }
    }
  }
}

.offer{
  padding-top: 3.75em;
  .map{
    margin-top: -6em;
    width: 100%;
    height: 42.375em;
  }
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 28.5em) 27.5em;
    gap: 1em;
  }
  &__content{
    max-width: 48.5em;
    .text{
      margin-top: 1.875em;
    }
  }
  &__title{
    max-width: 20em;
    font-size: 2em;
    line-height: 150%;
    font-weight: 700;
  }
  &__bottom{
    position: relative;
    z-index: 2;
    width: 73.125em;
    max-width: 100%;
    margin: 1.38em auto 0;
    padding: 2.5em .5em 2em;
    border-radius: 0.4375em;
    border: 1px solid #E8E8EE;
    background: #FFF;
    box-shadow: 0 .1875em .625em rgba(0, 0, 0, 0.13);
    .text30{
      text-align: center;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5em / 4));
    gap: 2.5em;
    margin-top: 1.25em;
  }
}

.offer-card{
  padding: 1.25em 1.5em;
  border-radius: 0.4375em;
  background: #F5F5F5;
  @include lastM(0 0 1.37em);
  &__desc{
    margin-top: 1.56em;
    .text14{
      line-height: 158%;
    }
  }
}

.prices{
  padding: 2.125em 0 3.81em;
  background: #F5F5F5;
}

.prices-table{
  margin-top: 1.875em;
  table{
    width: 100%;
    border-spacing: 0;
    border-collapse: separate;
  }
  th,
  td{
    text-align: center;
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
  th{
    font-weight: 700;
    height: 3.5em;
    background: #E4E3E9;
    &:first-child{
      border-radius: .4375em 0 0 .4375em;
    }
    &:last-child{
      border-radius: 0 .4375em .4375em 0;
    }
  }
  tbody{
    tr{
      background-color: #fff;
      &:nth-child(odd){
        td{
          background: #FFF;
          height: 3.75em;
        }
      }
      &:nth-child(even){
        td{
          height: 3.875em;
          position: relative;
          &:first-child:before{
            border-radius: .4375em 0 0 .4375em;
          }
          &:last-child:before{
            border-radius: 0 .4375em .4375em 0;
          }
          &:before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #E4E3E9;
            background: rgba(228, 227, 233, 0.30);
          }
          span{
            position: relative;
            z-index: 2;
          }
        }
      }
    }
  }
}

.akb-feedback{
  background: #1C195E;
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.give{
  padding: 3.75em 0 3.75em 1.875em;
  color: #fff;
  background-image: url(../img/arrow3.png);
  background-size: 25em auto;
  background-position: right  top;
  background-repeat: no-repeat;
  .text30{
    color: $yellow;
  }
  .m-btn{
    padding: 0;
    text-transform: uppercase;
  }
  @media screen and (min-width: 993px){
    .req-form__grid{
      grid-template-columns: calc(100% - 13.125em - .9375em) 13.125em;
      gap: .9375em;
    }
  }
}

.calc{
  position: relative;
  min-height: 100%;
  padding: 3.75em 1.875em 3.25em 0;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 3000px;
    height: 100%;
    background: #E4E3E9;
  }
}

.calc-form{
  position: relative;
  z-index: 2;
  max-width: 26em;
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1.25em);
  }
  &__field{
    align-items: center;
    display: grid;
    grid-template-columns: 7em calc(100% - 7em);
    &-label{
      font-size: .875em;
      line-height: 129%;
      display: block;
      color: $gray;
      font-weight: 500;
      margin-bottom: .33em;
      @media screen and (min-width: 993px){
        &:not(._small){
          margin: 0;
          height: 2.85em;
          display: flex;
          align-items: center;
          align-self: flex-start;
        }
      }
    }
  }
  .fg{
    .nice-select,
    input{
      border: none;
    }
  }
  .filter-slider__item{
    margin: 0 0 .25em;
  }
  &__checks{
    display: flex;
    align-items: center;
    .m-check{
      @include lastM(0 1.625em 0 0);
    }
  }
  &__result{
    margin-top: 1.875em;
    span{
      white-space: nowrap;
    }
  }
}


@media screen and (max-width: 992px){
  .old-akb{
    &__inner{
      &:before,
      &:after{
        display: none;
      }
    }
    &__title{
      font-size: 1.375em;
    }
    &__img{
      display: none;
    }
    .breadcrumbs{
      padding-bottom: 1.5em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .categories2{
    @media screen and (min-width: 577px){
      .category-card{
        &__top{
          padding-right: 4em;
        }
        .text22{
          font-size: 1em;
        }
      }
    }
  }

  .faq{
    padding: 1.25em 0;
    &__items{
      gap: 1em;
      margin-top: 1em;
    }
  }

  .become-client{
    &__img{
      display: none;
    }
    &__content{
      width: 100%;
      padding: 2em 0;
    }
    &__phone{
      a{
        font-size: 1.75em;
      }
    }
  }

  .offer{
    padding-top: 2em;
    &__top{
      grid-template-columns: 100%;
    }
    &__content{
      .text{
        margin-top: 1em;
      }
    }
    &__sidebar{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__title{
      font-size: 1.375em;
      line-height: 125%;
    }
    .map{
      margin-top: -2em;
    }
    &__bottom{
      padding-top: 1em;
      padding-bottom: 1em;
    }
    &__items{
      margin-top: .5em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .offer-card{
    margin: 0 !important;
  }

  .prices{
    padding: 1.25em 0;
  }

  .prices-table{
    margin: 1.25em 0 0 -.9375em;
    width: calc(100% + 1.875em);
    padding: 0 .9375em;
    overflow-x: auto;
    table{
      min-width: 40em;
    }
    th,td{
      span{
        font-size: .875em;
      }
    }
  }

  .give{
    padding: 1.25em 0 1.25em 1.25em;
  }

  .calc{
    padding: 1.25em 1.25em 1.25em 0;
  }

  .calc-form{
    &__fields{
      gap: 1em;
      margin-top: 1em;
    }
    &__field{
      display: block;
    }
    &__result{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .old-akb{
    &__content{
      max-width: unset;
      width: 100%;
      padding-bottom: 1.5em;
    }
    &__nav{
      .m-btn{
        span{
          font-size: .875em;
        }
      }
    }
    &__hint{
      align-items: flex-start;
    }
  }

  .categories2{
    .category-card{
      min-height: unset;
    }
  }

  .qa-card{
    &__show{
      padding: .5em .75em;
    }
    &__hidden{
      padding: .5em .75em 0;
    }
  }

  .become-client{
    &__links{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .offer{
    &__sidebar,
    &__items{
      grid-template-columns: 100%;
    }
    .map{
      margin-top: -1em;
      height: 25em;
    }
  }

  .offer-card{
    padding: .625em 1em;
    &__desc{
      margin-top: .25em;
    }
  }

  .prices-table{
    table{
      font-size: .75em;
    }
    th,td{
      text-align: left;
      padding: 0 .75em;
      height: 2.75em !important;
    }
  }

  .akb-feedback{
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .give{
    padding: 1.25em 0;
  }

  .calc{
    padding: 1.25em 0;
    &:before{
      right: -.9375em;
    }
  }
}
